














import { Component, Emit, Prop, Vue } from 'vue-property-decorator'

@Component
export default class ButtonExtension extends Vue {
  @Prop({ required: true })
  cell?: any

  @Prop({ required: true })
  disabled?: boolean

  @Emit('change')
  onChange () { /**/ }

  onKeyDownEnter () {
    this.cell.value = !this.cell.value
  }
}
