

























import { Component, Emit, Prop, Vue } from 'vue-property-decorator'
import VeditableCell from '@/components/veditable/VeditableCell.vue'

@Component({
  components: {
    VeditableCell,
  },
})
export default class VeditableRow extends Vue {
  @Prop({ required: true })
  columns?: any

  @Prop({ required: true })
  columnRegulations?: any

  @Prop({ required: true })
  rowIndex?: number

  @Prop({ required: true })
  numberOfLines?: boolean

  @Prop({ required: true })
  disabled?: boolean

  @Prop({ required: true })
  floatingColumns?: number

  @Prop({ required: true })
  isLastRow?: boolean

  @Emit('cellClicked')
  onCellClicked (params: any) {
    return {
      x: params.x,
      y: params.y,
    }
  }
}
